<template>

  <el-dialog :visible.sync="dialog_visible" center title="" width="400px"  :close-on-click-modal="false" :close-on-press-escape="false">
    <el-form ref="change_password_ref" :model="this" label-position="right" label-width="80px">
      <el-form-item label="" prop="oldPassword">
      <el-input v-model="oldPassword" type="password" :show-password="true"></el-input>
    </el-form-item>
      <el-form-item label="" prop="newPassword">
        <el-input v-model="newPassword" type="password" :show-password="true"></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirmPassword">
        <el-input v-model="confirmPassword" type="password" :show-password="true"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"  :disabled="oldPassword==''||newPassword==''|| confirmPassword==''"  :loading="loading" @click="change_password"> </el-button>
        <el-button @click="dialog_visible = false"> </el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
export default {
  name: "ChangePassword",
  data() {
    return {
      dialog_visible: false,
      oldPassword: "",
      newPassword: "",
      confirmPassword: "",
      loading:false
    }
  },
  methods: {
    change_password() {
      if (this.newPassword != this.confirmPassword) {
        this.$notify.error({
          title: '',
          duration: 3000,
          message: ``,
        });
        return
      }
      this.loading=true
      this.axios_get('/management/signature').then(res=>{
        if(res.data.code===0){
          let oldPassword=window.sha256_digest(window.sha256_digest(this.oldPassword + ":" + res.data.data.signature) + ":" + res.data.data.nonce)
          let nonce=res.data.data.nonce
          let netPassword=window.sha256_digest(this.newPassword + ":" + res.data.data.signature)
          let request={
            oldPassword:oldPassword,
            newPassword:netPassword,
            nonce:nonce
          }
          this.post_data(`/management/password`, request).then(res => {
            this.loading=false
            if (res.data.code === 0) {
              this.$refs.change_password_ref.resetFields()
              this.dialog_visible = false
            }
          })
        }else{
          this.loading=false
        }
      })
    }
  }
}
</script>

